import {Component,HostListener } from '@angular/core';
import {DataService} from "./data.service"
import {enableProdMode} from '@angular/core';
import {TimelineData} from "../models/timeline.model"
import {UserResponse} from "../models/userResponse.model"



enableProdMode();


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

@HostListener('window:resize', ['$event'])
export class AppComponent {

  @HostListener('document:keypress', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {

    this.key = event.key;
    if(this.key=="Enter"){
      this.loadNextQuestion()
    }
  }

  key:string
  title = 'Timeline Evaluation';
  currentQuestionCounter = 0;
  totalQuestionCounter = 24
  visWidth = window.screen.width;
  visHeight = window.screen.height;
  questions:TimelineData[]=[];
  ifInputNotProvided=true;
  userInput="";
  imgHome = "../assets/";
  imgName = ""
  currentQuestion=""
  time: Date;
  userResponseLocalVar:UserResponse
  startTime:Date
  startTimeFirstTimeDefine:boolean = false
  randomizedQuestionOrder = []
  showInstructions = true
  showSurvey = false
  byPassQuestion = false // Need to set to false before goes into production
  testingParameter = true // if this is true then a hardcoded case will show for testing
  userIdentifier = ""+Math.floor(Math.random() * 10000) + 1
  currentEncoding:string// this variable stores the type of visual encoding
  padding:string //this variable will store the padding to provide based on layout of the visualization

  userResponseFinalData:UserResponse[]=[]

  constructor(private data:DataService,private  userResponse:UserResponse){
  }

  ngOnInit(){

    //Step1 : a. Set user demographics information: Set the information about the user upfront, we need user demographics information
    // We might need this for the current evaluation, also needs some thought
    this.userResponseLocalVar=new UserResponse();


    //Step 3: Initialize the time for the evaluation, needs to start at the end of instruction
    // this.data.getClock().subscribe(time => {
    //   this.time = time;
    //   if(!this.startTimeFirstTimeDefine){
    //     this.startTime = time;
    //     this.startTimeFirstTimeDefine = true;
    //   }
    // });


    //Step 2: Load the questions from the database
     this.data.getQuestions().subscribe(response => {

       this.questions=response;

       if(this.testingParameter){
        // this.randomizedQuestionOrder=[3, 23, 11, 18, 5, 10, 24, 4, 16, 12, 17, 13, 2, 20, 6, 15, 19, 8, 21, 1, 7, 14, 9, 22]
          this.randomizedQuestionOrder=[19, 4, 2, 9, 6, 24, 20, 15, 1, 21, 23, 3, 13, 22, 7, 5, 10, 17, 8, 11, 14, 18, 16, 12]



         this.imgName=this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter]-1].file


         //this.imgName="index.png"
       }
       else{
         //Normal Step: Testing the randomization
         this.randomizedQuestionOrder=this.testRandomization()

         //Loading Images with randomized order
         this.imgName=this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter]-1].file
       }

       //Question
       this.currentQuestion=this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter]-1].question


       //Data to record at the starting of the evaluation
       this.userResponseLocalVar.name = this.userIdentifier

       // //Step 5: Record the time and also do it when the the survey is initiated
       // this.userResponseLocalVar.time=this.time;
       //Step 6: Store the timelineQuestionId and also do it when the surey is initiated
       this.userResponseLocalVar.timelineQuestionId=this.questions[0].id

       //Step 7: Get the type of Visual Encoding
       this.currentEncoding = this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter]-1].visualEncoding
       this.padding=this.returnPadding(this.currentEncoding)


       }
     )

  }

  userInputChange(){
    this.ifInputNotProvided = ((this.userInput=="")) ? true : false
  }

  loadNextQuestion():void
  {
    if(this.currentQuestionCounter<this.totalQuestionCounter-1)
    {

      //Step 1: Save data
      this.saveData();

      //Step 2: Clear the input field and reset the inInputNotProvided option
      this.userInput = "";
      this.ifInputNotProvided = ((this.userInput=="")) ? true : false

      // //Step 3:Change the question
      // this.currentQuestion=this.questions[this.currentQuestionCounter+1].question
      //
      // //Step 4: Change the visualization
      // this.imgName=this.questions[this.currentQuestionCounter+1].file


      //Step 3:Change the question
      this.currentQuestion=this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter+1]-1].question

      //Step 4: Change the visualization
      this.imgName=this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter+1]-1].file


      //Step 7: Get the type of Visual Encoding
      this.currentEncoding = this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter+1]-1].visualEncoding

      this.padding=this.returnPadding(this.currentEncoding)
      //Step 5: Change the counter
      this.currentQuestionCounter += 1;

    }

    else{

      // Save the answers for final set of questions
      this.saveData();
      this.downloadData();

     //Test
      console.log(this.userResponseFinalData)
      alert("End of Survey!")

    }
  }


  returnPadding(encoding):string{
    if(encoding=="circle" ){
      return "27"
    }
    else if(encoding=="spiral"){
      return "27"
    }
    else{
      return "0"
    }
  }

  saveData():void{

    //Initializing step
    this.userResponseLocalVar = new UserResponse();
    this.userResponseLocalVar.name = this.userIdentifier

    //Step 1: Record the time and also do it when the the survey is initiated
    this.userResponseLocalVar.endTime = new Date();
    this.userResponseLocalVar.startTime = this.startTime;
    console.log((this.userResponseLocalVar.endTime.getTime()-this.userResponseLocalVar.startTime.getTime()))
    console.log(((this.userResponseLocalVar.endTime.getTime()-this.userResponseLocalVar.startTime.getTime())/1000))
    this.userResponseLocalVar.duration = ((this.userResponseLocalVar.endTime.getTime()-this.userResponseLocalVar.startTime.getTime())/1000)
    this.startTime = this.time;

    // //Step 2: Record the answer
    this.userResponseLocalVar.response=this.userInput

    //Step 3: Store the timelineQuestionId and also do it when the surey is initiated
    //this.userResponseLocalVar.timelineQuestionId=this.questions[this.currentQuestionCounter].id

    //Test
    this.userResponseLocalVar.timelineQuestionId=this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter]-1].id

    //Recording the visual enocding
    this.userResponseLocalVar.visualEncoding= this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter]-1].visualEncoding
    this.userResponseLocalVar.family= this.questions[this.randomizedQuestionOrder[this.currentQuestionCounter]-1].family

    //Step 4: Cross checking the data stored
    console.log(this.userResponseLocalVar)
    this.userResponseFinalData.push(this.userResponseLocalVar);
  }

  downloadData():void{

    //Code copied from StackOverflow
    var theJSON = JSON.stringify(this.userResponseFinalData);
    var uri = "data:application/json;filename=filename.json,charset=UTF-8" + encodeURIComponent(theJSON);

    var a = document.createElement('a');
    a.href = uri;
    a.innerHTML = "Right-click and choose 'save as...'";
    document.body.appendChild(a);

  }


  testRandomization():number[]{

    let localCounter=0
    let counterExceeded = false
    let firstNumber=Math.ceil(Math.random() * Math.floor(24));

    let arrIndex=[firstNumber]
    const indexUsed=[firstNumber]

    for(let i=1; i<24;i++){
      let checkCondition = true

      while(checkCondition){
        localCounter += 1
        if(localCounter>1000){
          counterExceeded = true
          break
        }
        console.log(localCounter+1)
        let randomIndex =   Math.ceil(Math.random() * Math.floor(24));
        if(indexUsed.indexOf(randomIndex) ==-1){
          if(Math.abs(arrIndex[i-1]-randomIndex)==1 || Math.abs(arrIndex[i-1]-randomIndex) == 8 ){
            //do nothing
          }
          else{
            arrIndex.push(randomIndex)
            indexUsed.push(randomIndex)
            checkCondition = false
          }
        }

      }

    }
    if(counterExceeded){
      console.log([3, 23, 11, 18, 5, 10, 24, 4, 16, 12, 17, 13, 2, 20, 6, 15, 19, 8, 21, 1, 7, 14, 9, 22])
      return ( [3, 23, 11, 18, 5, 10, 24, 4, 16, 12, 17, 13, 2, 20, 6, 15, 19, 8, 21, 1, 7, 14, 9, 22])
    }
    else{
      console.log(arrIndex)
      return (arrIndex)
    }

  }

  moveToSurvey():void
  {
    console.log("move to survey")
    this.showInstructions=false
    this.showSurvey=true

    //Moving the clock time to this point
    this.data.getClock().subscribe(time => {
      this.time = time;
      if(!this.startTimeFirstTimeDefine){
        this.startTime = new Date();
        console.log(this.startTime)
        this.startTimeFirstTimeDefine = true;
      }
    });
  }

}



